#sidebar{
    position: absolute;
    width: 300px;
    height: 90%;
    background: black;
   
    overflow: scroll;
    cursor: pointer;
}
#sidebar ul li {
    color: white;
    list-style: none;
    padding: 15px 10px;
    border-bottom: 1px solid rgb(100,100,100);
    font-size: 13px;
   
}
#HomeScreen{
    margin-left: 300px;
}
#AboutScreen,#ContactScreen{
    margin-left: 300px;
    display: none;
}

.step-progress
{
    color:red;   
    cursor: pointer;
}



